@use '../../0-base/1-tools/scss/breakpoint';
@use '../../0-base/1-tools/scss/button';
@use 'bourbon/core/bourbon';
@use 'settings/base';

.button {
  /* critical:start */
  @include button.btn-default;
  /* critical:end */
  @include breakpoint.media('print') {
    display: none;
  }

  &--clear {
    /* critical:start */
    @include button.button--clear;
    /* critical:end */

    &.button--fill-animation {
      @include button.btn-animate(base.$button__clear-color, base.$color-white, base.$button__clear-color);
    }
  }

  &--clear_inverted {
    /* critical:start */
    @include button.btn-style(
      $text-color: base.$button__clear-inverted-color,
      $bg-color: base.$button__clear-inverted-bg-color,
      $border-color: base.$button__clear-inverted-border-color,
      $text-color-hover: base.$button__clear-inverted-color-hover,
      $bg-color-hover: base.$button__clear-inverted-bg-color-hover,
      $border-color-hover: base.$button__clear-inverted-border-color-hover,
      $text-color-active: base.$button__clear-inverted-color-active,
      $bg-color-active: base.$button__clear-inverted-bg-color-active,
      $border-color-active: base.$button__clear-inverted-border-color-active
    );
    padding-left: 0;
    /* critical:end */

    &:focus,
    &:hover {
      padding: base.$button__padding;
    }

    &.button--fill-animation {
      @include button.btn-animate(base.$color-white, base.$color-brand, base.$color-white);
    }
  }

  &--solid {
    /* critical:start */
    @include button.btn-style(
      $text-color: base.$button__solid-color,
      $bg-color: base.$button__solid-bg-color,
      $border-color: base.$button__solid-border-color,
      $text-color-hover: base.$button__solid-color-hover,
      $bg-color-hover: base.$button__solid-bg-color-hover,
      $border-color-hover: base.$button__solid-border-color-hover,
      $text-color-active: base.$button__solid-color-active,
      $bg-color-active: base.$button__solid-bg-color-active,
      $border-color-active: base.$button__solid-border-color-active
    );
    /* critical:end */

    &.button--fill-animation {
      @include button.btn-animate(base.$button__solid-color, base.$button__solid-bg-color, base.$button__solid-border-color);
    }
  }

  /// @deprecated
  /// `.button--inverted` is deprecated! use `.button--solid_inverted` instead
  &--inverted,
  &--solid_inverted {
    /* critical:start */
    @include button.button--solid-inverted;
    /* critical:end */

    &.button--fill-animation {
      @include button.btn-animate(base.$button__solid-inverted-color, base.$color-white, base.$button__solid-inverted-border-color);
    }
  }

  &--outline {
    /* critical:start */
    @include button.btn-style(
      $text-color: base.$button__outline-color,
      $bg-color: base.$button__outline-bg-color,
      $border-color: base.$button__outline-border-color,
      $text-color-hover: base.$button__outline-color-hover,
      $bg-color-hover: base.$button__outline-bg-color-hover,
      $border-color-hover: base.$button__outline-border-color-hover,
      $text-color-active: base.$button__outline-color-active,
      $bg-color-active: base.$button__outline-bg-color-active,
      $border-color-active: base.$button__outline-border-color-active
    );
    border-width: base.$button__border-width;
    border-style: solid;
    /* critical:end */

    &.button--fill-animation {
      @include button.btn-animate(base.$button__outline-color, base.$color-white, base.$button__outline-color);
    }
  }

  &--outline_inverted {
    /* critical:start */
    @include button.btn-style(
      $text-color: base.$button__outline-inverted-color,
      $bg-color: base.$button__outline-inverted-bg-color,
      $border-color: base.$button__outline-inverted-border-color,
      $text-color-hover: base.$button__outline-inverted-color-hover,
      $bg-color-hover: base.$button__outline-inverted-bg-color-hover,
      $border-color-hover: base.$button__outline-inverted-border-color-hover,
      $text-color-active: base.$button__outline-inverted-color-active,
      $bg-color-active: base.$button__outline-inverted-bg-color-active,
      $border-color-active: base.$button__outline-inverted-border-color-active
    );
    border-width: base.$button__border-width;
    border-style: solid;
    /* critical:end */

    &.button--fill-animation {
      @include button.btn-animate(base.$color-white, base.$color-brand, base.$color-white);
    }
  }

  &--is-loading,
  &--is-successful {
    padding-left: 0;
    padding-right: 0;

    span {
      height: 0;
      visibility: hidden;
    }
  }

  &--is-loading {
    cursor: progress;

    &::after {
      @include bourbon.size(1.3em); // = default button line-height

      animation: btnSpinner 1.5s infinite 0.1s linear;
      border-radius: 50%;
      background-color: transparent;
      border: 2px solid base.$button__solid-color;
      border-top-color: transparent;
      opacity: 1;
    }
  }

  &--is-successful {
    &::before {
      content: '✓';
      transform: scale(2);
      opacity: 1;
    }
  }

  &--small {
    /* critical:start */
    @include button.button--small;
    /* critical:end */
  }

  &--large {
    /* critical:start */
    @include button.button--large;
    /* critical:end */
  }

  &--text-icon {
    display: inline-flex;

    .icon {
      width: base.$button__font-size;
      height: base.$button__font-size;
      vertical-align: baseline;
    }

    .button {
      &__icon--before,
      &__icon--after {
        width: base.$button__font-size;
        height: base.$button__font-size;
        padding-top: 0.1rem;
      }
    }
  }

  &--icon-animation {
    &:hover {
      .button__icon--after {
        animation: hover-effect 0.6s;
      }
    }
  }

  &__icon--before {
    margin-right: 0.5rem;
  }

  &__icon--after {
    margin-left: 0.5rem;
  }
}

@keyframes btnSpinner {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.button--deals {
  border: 1px solid #fff;
  background: transparent;
  color: #fff;
  box-shadow: 0 0 15px rgba(base.$color-black, 0.4);

  &.button--without_box-shadow {
    box-shadow: none;
  }

  &:hover {
    background: transparent;
    opacity: 0.8;
    border: 1px solid #fff;
  }
}

@keyframes hover-effect {
  0% {
    padding-left: 0;
    opacity: 1;
  }

  60% {
    padding-left: 1rem;
    opacity: 0;
  }

  75% {
    padding-left: 0;
    opacity: 0;
    margin-left: 0;
    margin-right: 0.5rem;
  }

  90% {
    padding-left: 0.2rem;
    opacity: 1;
    margin-left: 0.2rem;
    margin-right: 0.3rem;
  }

  100% {
    margin-left: 0.5rem;
    margin-right: 0;
    padding-left: 0;
    opacity: 1;
  }
}

