@use '../../../0-base/1-tools/scss/helpers';
@use 'bourbon/core/bourbon';
@use 'settings/base';

@mixin icons-example {
  .icons-example {
    @include helpers.clearlist;

    display: flex;
    flex-wrap: wrap;
    justify-content: center;

    li {
      @include bourbon.size(7rem);
      align-items: center;
      display: flex;
      flex-direction: column;
      justify-content: center;
    }

    svg {
      @include bourbon.size(3rem);
      box-shadow: 0 0 0.5rem base.$color-black-15;
      padding: 0.75rem;
    }

    pre {
      color: base.$color-black-50;
      font-size: base.$font-size-small;
      margin: 0.3rem 0 0;
    }
  }
}
