@use '../../../0-base/1-tools/scss/grid';
@use '../../../0-base/1-tools/scss/helpers';
@use '../../../0-base/1-tools/scss/breakpoint';
@use 'bourbon/core/bourbon';
@use 'settings/base';

@if rm('modules.base.highlight-text') {
  .highlight-text-wrap {
    // CENTER
    text-align: center;

    @include breakpoint.media('≥m') {
      @include grid.grid-columns(8);
      @include grid.grid-push(2);
    }

    // ALIGN LEFT
    &--align_left {
      text-align: left;
      @include breakpoint.media('≥m') {
        @include grid.grid-push(0);
      }
    }

    // ALIGN LEFTIN
    &--align_leftin {
      text-align: left;
    }

    // ALIGN RIGHT
    &--align_right {
      text-align: left;

      @include breakpoint.media('≥m') {
        @include grid.grid-push(4);
      }
    }
  }

  .highlight-text {
    &__item {
      &__icon {
        @include bourbon.size(2.65em);
        @include helpers.icon-color(base.$color-brand);
      }

      &__text {
        font-size: base.$font-size-default;
        line-height: base.$line-height-default;
        font-weight: helpers.get-font-weight(base.$highlight-text__font-weight);
        color: base.$highlight-text__color;

        @include breakpoint.media('≥s') {
          font-size: base.$font-size-large;
          line-height: base.$line-height-large;
        }
      }
    }

    h1 {
      margin-top: 0;
      margin-bottom: 1em;
    }
  }
}
