@use '../../../0-base/1-tools/scss/helpers';
@use 'settings/base';
@use 'settings/global';

.env-flag {
  position: fixed;
  top: -5.3em;
  right: -5.3em;
  width: 10em;
  height: 10em;
  transform: rotate(45deg);
  font-size: 0.8rem;
  font-weight: helpers.get-font-weight(bold);
  font-family: sans-serif;
  text-transform: uppercase;

  cursor: ne-resize;
  z-index: global.$env-flag-zindex;

  &::after {
    position: absolute;
    bottom: 0.5em;
    width: 100%;
    color: base.$color-white;
    text-align: center;
  }

  @include helpers.no-print;

  &--local {
    background-color: rgba(#3ca8ff, 0.7);
    letter-spacing: 0.1em;

    &::after {
      content: 'Local';
    }
  }

  &--tech {
    background-color: rgba(255, 0, 0, 0.7);
    letter-spacing: 0.2em;

    &::after {
      content: 'Dev';
    }
  }

  &--review {
    background-color: rgba(255, 129, 0, 0.8);
    letter-spacing: 0.1em;

    &::after {
      content: 'Review';
    }
  }
}
