@use '../../0-base/1-tools/scss/grid';
@use '../../0-base/1-tools/scss/sizes';
@use '../../0-base/1-tools/scss/breakpoint';
@use '../../0-base/0-settings/selector';

.content-headline {

  @include breakpoint.media('≥s') {
    @include grid.grid-columns(10);
    @include grid.grid-push(1);
  }

  @include breakpoint.media('≥m') {
    @include grid.grid-columns(8);
    @include grid.grid-push(2);
  }

  &--align_left {
    margin-left: 0 !important;
  }

  &--align_leftin {
    @include breakpoint.media('≥s') {
      @include grid.grid-push(0);
    }
  }
}

#{selector.$template--content-100} {
  .content-headline {
    &--align_left {
      @include breakpoint.media('≥m') {
        @include grid.grid-columns(6);
      }
    }

    &--align_leftin {
      @include grid.grid-columns(8);
      @include grid.grid-push(0);

      @include breakpoint.media('≥m') {
        @include grid.grid-columns(6);
        @include grid.grid-push(0);
      }
    }
  }
}

#{selector.$template--content-70} {
  .content-headline {
    @include breakpoint.media('≥s') {
      @include grid.grid-columns(10);
      @include grid.grid-push(1);
    }

    &--align_left,
    &--align_leftin {
      @include breakpoint.media('≥s') {
        @include grid.grid-columns(10);
        @include grid.grid-push(0);
      }

      @include breakpoint.media('≥m') {
        @include grid.grid-columns(8);
        @include grid.grid-push(0);
      }
    }
  }
}

#{selector.$template--content-30} {
  @include breakpoint.media('≥m') {
    .content-headline {
      @include grid.grid-columns(12);
      @include grid.grid-push(0);
      margin-bottom: 1em;

      h2,
      h3 {
        font-size: 1.2em;
      }

      &__subheader {
        font-size: 1.1em;
      }
    }
  }
}

.l-header-only-wrap {
  @include sizes.vertical-padding;

  /* critical:start */
  padding-bottom: 0;
  /* critical:end */

  @include breakpoint.media('≥s') {
    /* critical:start */
    padding-bottom: 0;
    /* critical:end */
  }

  @include breakpoint.media('≥m') {
    /* critical:start */
    padding-bottom: 0;
    /* critical:end */
  }

  .content-headline {
    margin-bottom: 0;

    @include breakpoint.media('≥m') {
      /* critical:start */
      margin-bottom: 1em;
      /* critical:end */
    }
  }
}
