/**
 * ## TYPO3 Content Element text-media
 */

@use '../../../0-base/1-tools/scss/aspect-ratio';
@use '../../../0-base/1-tools/scss/helpers';
@use '../../../0-base/1-tools/scss/breakpoint';
@use '../../../1-atoms/rich-text/rich-text';
@use 'bourbon/core/bourbon';

.text-media {
  @include bourbon.clearfix;

  .gallery {
    display: block; // ie fix
  }

  &--center {
    .media {
      text-align: center;
    }

    .gallery__column {
      text-align: center;

      figure {
        margin: 0 auto;
      }
    }
  }

  &--right {
    &.text-media--intext {
      .gallery {
        @include breakpoint.media('≥s') {
          max-width: 50%;
          min-width: 0;
          float: right;
          margin-left: 2em;
        }

        @include breakpoint.media('print') {
          width: 30%;
        }
      }
    }

    &.text-media--above,
    &.text-media--below {
      .gallery__row {
        @include bourbon.clearfix;
      }

      .gallery__column {
        float: right;

        @include helpers.browser(ie11) {
          img {
            float: right;
          }

          figcaption {
            float: right;
            text-align: right;
          }
        }

      }
    }
  }

  &--left {
    &.text-media--intext {
      .gallery {
        @include breakpoint.media('≥s') {
          max-width: 50%;
          min-width: 0;
          float: left;
          margin-right: 2em;
        }

        @include breakpoint.media('print') {
          width: 30%;
        }
      }
    }
  }

  &--nowrap {
    .bodytext {
      overflow: visible;

      @include breakpoint.media('≥s') {
        overflow: hidden;
      }

      @include breakpoint.media('print') {
        overflow: visible;
      }
    }
  }

  .bodytext {
    @extend %rich-text;
  }

  p:last-child {
    margin-bottom: 0;
  }

  .gallery {
    &__row {
      flex: 0 0 100% !important;
      max-width: 100% !important;
    }

    &__column {
      margin-bottom: 1em;

      > * + * {
        padding-top: 1em;
      }

      @include breakpoint.media('print') {
        @include bourbon.margin(null auto);

        max-width: 60%;
      }
    }

    figure {
      display: table;
      table-layout: fixed;

      @include helpers.browser(ie11) {
        display: block;
      }

      a {
        display: table-cell;

        @include helpers.browser(ie11) {
          display: block;
        }
      }

      figcaption {
        table-layout: fixed;
        display: table-caption;
        caption-side: bottom;
        text-align: left;

        @include helpers.browser(ie11) {
          display: block;
        }
      }
    }
  }
}

.text-media--below {
  .gallery {
    &__column {
      margin-bottom: 0;
      margin-top: 1em;
    }
  }
}

.media {
  text-align: center;

  @include breakpoint.media('≥s') {
    text-align: start;
  }

  &__video {
    &.video-iframe--apsect-ratio-calculated {
      @include aspect-ratio.aspect-ratio;

      .text-media--left &,
      .text-media--right & {
        @include breakpoint.media('≥s') {
          width: 50vw;
          max-width: 100%;
        }
      }
    }
  }
}
