@use 'settings/base';

@mixin font-sizes-example {
  .styleguide-font-size {
    margin-bottom: 1em;

    &__h1 {
      font-size: base.$h1-font-size-s;
    }

    &__h2 {
      font-size: base.$h2-font-size-s;
    }

    &__h3 {
      font-size: base.$h3-font-size-s;
    }

    &__large {
      font-size: base.$font-size-large;
    }

    &__default {
      font-size: base.$font-size-default;
    }

    &__small {
      font-size: base.$font-size-small;
    }

    &__tiny {
      font-size: base.$font-size-tiny;
    }
  }
}
