/**
 * # Content
 */

@use '../../0-base/1-tools/scss/breakpoint';
@use '../../1-atoms/anchor/anchor';

.content-wrap {
  /* critical:start */
  @extend %anchor-target;
  /* critical:end */

  &:focus {
    outline: 0;
  }

  @include breakpoint.media('print') {
    background-color: transparent;
  }
}
