/// @see https://github.com/daneden/animate.css

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

@keyframes fadeOut {
  from { opacity: 1; }
  to { opacity: 0; }
}

.animate {
  animation-fill-mode: both;

  @media (print), (prefers-reduced-motion) {
    animation: unset !important;
    transition: none !important;
  }
}
