/**
 * ## General
 */

@use '../../0-base/1-tools/scss/helpers';
@use '../../0-base/1-tools/scss/breakpoint';
@use 'settings/base';

/* critical:start */
h1,
h2,
h3,
.headline {
  @include helpers.type-reset;

  font-weight: helpers.get-font-weight(regular);
  font-style: normal;

  @include breakpoint.media('print') {
    page-break-after: avoid;
  }

  a {
    text-decoration: none !important;
  }
}
/* critical:end */

/**
 * ## h1
 */

h1,
.headline-1 {
  /* critical:start */
  font-size: base.$h1-font-size;
  line-height: base.$h1-line-height;
  font-weight: helpers.get-font-weight(base.$h1-font-weight);
  color: base.$h1-font-color;
  text-transform: base.$h1-text-transform;
  margin-bottom: 0.5em;
  /* critical:end */

  @include breakpoint.media('≥s') {
    /* critical:start */
    font-size: base.$h1-font-size-s;
    line-height: base.$h1-line-height-s;
    margin: base.$h1-margin;
    /* critical:end */
  }

  a {
    text-decoration: none;
  }
}

